a {
  text-decoration: none;
}

ul,
ol,
li {
  list-style: none;
}

@font-face {
  font-family: "Roboto";
  src: url(../styles/assets/fonts/Roboto/Roboto-Regular.ttf);
}

@font-face {
  font-family: "Revalia";
  src: url(../styles/assets/fonts/Revalia/Revalia.ttf);
}

body {
  margin: 0;
  height: 100vh;
  font-family: "Roboto";
  color: #ffffff;

  background: #1a1a1a;
  background-image: url(./images/backgroung.svg);
}

#root {
  display: flex;
  flex-direction: column;

  min-height: 100%;
}

.container {
  max-width: 1106px;
  padding: 0 15px;
  margin: 0px auto;
  box-sizing: border-box;
}

main {
  flex: 1 1 auto;
  padding-top: 100px;
}

.gold-color {
  color: #f4fa1a;
}
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
}

.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #1a1a1a;
  z-index: 2;
}

.header-body {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}

.burger-btn {
  background-color: #1a1a1a;
  border: none;

  color: #f4fa1a;
  font-size: 40px;

  cursor: pointer;
  transition-duration: 2s;
}

.burger-btn:hover {
  color: #f2f594;
}

.burger-btn {
  display: none;
}

.menu-list {
  display: flex;
  position: relative;
  z-index: 2;
  padding: 0;
  margin: 0;
}

.menu-item {
  width: 102px;
}
@media (max-width: 767px) {
  .header-body {
    height: 50px;
  }

  .burger-btn {
    display: block;
    position: relative;
    z-index: 3;
  }
  .header-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background-color: #636060;
    padding: 70px 0 0 0;
    transition-duration: 1s;
    transform: translateX(-100%);
  }
  .header-menu.active {
    transform: translateX(0);
    top: 0;
  }
  .menu-list {
    display: block;
    padding-left: 20px;
  }

  .menu-list li {
    margin-bottom: 20px;
  }

  .blur {
    transition-duration: 1s;
  }
  .blur.active {
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(2px);
    position: absolute;
    left: 0;
  }
  main {
    padding-top: 70px;
  }
}
.header-logo {
  position: relative;
  display: flex;
  z-index: 3;
}

.burger-button {
  margin: 0;
  display: flex;
}

.nav-link {
  text-decoration: none;
  color: inherit;
  font-family: "Roboto";
  font-style: normal;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  text-align: center;
}

.nav-link:hover {
  color: #f2f594;
}

.footer {
  width: 100%;
  height: 73px;

  font-size: 14px;
  background: rgba(26, 26, 26, 0.84);
}
.footer-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.social img {
  margin-left: 10px;
  width: 35px;
}

.block-Right {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.image-main-page {
  text-align: center;
}
.image-main-page > img {
  width: 70%;
}
.main-page,
.about,
.skills,
.contacts {
  display: flex;

  height: 100%;
  margin-top: 30px;
}

.main-page-content,
.about-content,
.skills-content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

@media (max-width: 767px) {
  .main-page-content,
  .about-content,
  .skills-content {
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 20px;
  }
}

.greetings {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 32px;
}

.name {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 800;

  font-size: 30px;
}
.profession {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 47px;
}

.my-button {
  box-sizing: border-box;
  color: #ffffff;
  width: 200px;
  height: 50px;
  font-size: 16px;
  line-height: 32px;
  border: 2px solid #f4fa1a;
  border-radius: 15px;
  background: #1a1a1a;

  transition-duration: 1s;
}

.my-button:hover {
  background: #f2f594;
  color: #1a1a1a;
}

.my-button:active {
  background: #f4fa1a;
  color: #1a1a1a;
}

.image-about {
  position: relative;
  width: 350px;
  height: 350px;
}

.image-about > img {
  position: absolute;
  top: 0;
  right: 0;
  width: 350px;
  border-radius: 20px;
  z-index: 20;
  animation: photo 2s forwards;
}

@keyframes photo {
  0% {
  }
  100% {
    top: -20px;
    right: -20px;
  }
}

.frame {
  position: absolute;

  z-index: 10;
  width: 350px;
  height: 350px;
  box-sizing: border-box;
  border: 3px solid #f4fa1a;
  border-radius: 20px;
}

.text-about {
  width: 50%;
}

.education {
  font-size: 14px;
}

.experience,
.form {
  width: 50%;
}

.progressbar {
  position: relative;
  width: 300px;
  height: 6px;
  background-color: #bbbbbb;
  border-radius: 1.5em;
  color: #ffffff;
}
.progressbar::before {
  content: "";
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 0;
  min-width: 0;
  max-width: 100%;
  background-color: #f4fa1a;
  border-radius: 1.5em;
  animation: progress 2s linear forwards;
}

@keyframes progress {
  0% {
    width: 0;
  }
  100% {
    width: calc(var(--width, 0) * 1%);
  }
}

.title-skill {
  display: flex;
  justify-content: space-between;
  margin: 8px 0;
  font-size: 12px;
}

.small-input {
  box-sizing: border-box;

  font-family: "Roboto";
  font-size: 16px;

  padding: 18px 15px;
  gap: 10px;

  width: 200px;
  height: 40px;

  background: #e2e1e1;
  border-radius: 10px;
}

.small-input::placeholder {
  font-family: "Roboto";
  font-size: 16px;

  color: #5f5f5f;
}

.small-input:focus {
  border: 2px solid #f4fa1a;
  outline: 0;
}

form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.big-input {
  box-sizing: border-box;

  font-family: "Roboto";
  font-size: 16px;

  padding: 18px 15px;

  width: 410px;
  height: 40px;

  background: #e2e1e1;
  border-radius: 10px;
}

.big-input::placeholder {
  font-family: "Roboto";
  font-size: 16px;

  color: #5f5f5f;
}

.big-input:focus {
  border: 2px solid #f4fa1a;
  outline: 0;
}

.message-input {
  box-sizing: border-box;

  font-family: "Roboto";
  font-size: 16px;

  padding: 18px 15px;

  width: 410px;
  height: 100px;

  background: #e2e1e1;
  border-radius: 10px;
  resize: none;
}

.message-input::placeholder {
  font-family: "Roboto";
  font-size: 16px;

  color: #5f5f5f;
}

.message-input:focus {
  border: 2px solid #f4fa1a;
  outline: 0;
}

.item-contacts {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.item-contacts > img {
  width: 30px;
  height: 30px;

  margin-right: 10px;
}

.item-contacts > a {
  color: inherit;
}

.contacts-content {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

@media (max-width: 767px) {
  .text-about {
    width: 100%;
  }
  .frame {
    left: -10px;
  }
  .image-about > img {
    left: -10px;
    animation: photoMobile 2s forwards;
  }
  @keyframes photoMobile {
    0% {
    }
    100% {
      top: -20px;
      left: 20px;
    }
  }
  .experience {
    width: 100%;
  }
  .contacts-content {
    flex-wrap: wrap;
    flex-direction: column-reverse;
    gap: 40px;
    padding-bottom: 40px;
  }
  .form {
    width: 100%;
    text-align: center;
  }
  form {
    flex-direction: column;
    align-content: center;
    align-items: center;
  }
  .small-input,
  .big-input,
  .message-input {
    width: 380px;
  }
}

.projects {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 500px;
}

.cards-projects {
  position: relative;
  display: flex;
  gap: 10px;
}
.project-column {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.card-item-down {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  box-sizing: border-box;
  background: #636060;
}

.card-item-up {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  box-sizing: border-box;
  background: #636060;
}

.card-small {
  height: 160px;
  width: 200px;
}

.card-big {
  height: 260px;
  width: 200px;
}

.card-average {
  height: 210px;
  width: 200px;
}

.card-item-up:active {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  animation: details 0.5s linear forwards;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}

.card-item-down:active {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  animation: details 0.5s linear forwards;
  position: absolute;
  bottom: 0;

  left: 0;
  z-index: 100;
}

@keyframes details {
  0% {
  }
  100% {
    position: absolute;

    height: 430px;
  }
}

h4 {
  margin: 5px 0;
  color: #f4fa1a;
}

.project-column {
  font-size: 13px;
}

.buttonProject {
  width: 150px;
  height: 40px;
}

.description {
  overflow: hidden;
}
i {
  color: #f4fa1a;
  margin-left: 15px;
  transition-duration: 0.5s;
}

i:hover {
  color: rgb(181, 184, 157);
}
